function modelShow (title = '', content = '?',callback = () => {}, showCancel = true,  cancelText = '', confirmText = '') {
	uni.showModal({
			title: title,
			content: content,
			showCancel: showCancel,
			cancelText: cancelText,
			confirmText: confirmText,
			cancelText: cancelText,
			success: function (res) {
					if (res.confirm) {
							// 
							setTimeout(() => {
								callback()
							}, 500)
					} else if (res.cancel) {
							// 
					}
			}
	});
}


