import * as config from './config.js';
import * as common from './common.js' //common
import * as db from './db.js' //common
import areas from '@/static/areas.json'
// 
const methodsToken = [
	'/shfwApi/auth'
];
function getAreas(){
	return areas;
}
function getWebUser(){
	const webUserstr = uni.getStorageSync('webUser');
	if(webUserstr!=""&&webUserstr!=null){
		return JSON.parse(webUserstr);	 	
	}else{
		return new Object();
	}
}
const post = (url, data, callback) => {
	uni.showLoading({
		title: ''
	});
	var requestHeader=new Object();
	requestHeader.Accept='application/json';
	requestHeader["Content-Type"]='application/x-www-form-urlencoded';
	// token
	if (url.indexOf("/shfwApi/auth")!=-1||url.indexOf("/shfwApi/appUpdate")!=-1
	||url.indexOf("/shfwApi/getArealistByCode")!=-1||url.indexOf("/shfwApi/registersysApi")!=-1
	) {
	
	}else{
		var webuser=getWebUser();
		if(webuser.token&&webuser.token!=null&&webuser.token!=""){
			requestHeader.Authorization="Bearer"+webuser.token;
		}else{//
			var param=new Object();
			param.onevent="indexupdate";
			common.redirectTo("./login",param);
			console.log("")
		}
	}
	
	/***************************/
	/* callback(apidata[method]);
	uni.hideLoading(); */
	uni.request({
		url: url,
		dataType: 'json',//json text
		data: {params:JSON.stringify(data)},
		header:requestHeader, 
		method:"POST",//GET\POST
		success: (res) => {
			uni.hideLoading();
			const result = res.data
			if (!result.status) {
				// 
				if (result.data === 14007 || result.data === 14006) {
					db.del("webUser");
					uni.showToast({
						title: result.msg,	
						icon: 'none',
						duration: 2000,
						complete: function () {
							var param=new Object();
							param.onevent="indexupdate";
							common.navigateTo("./login",param)
						}
					});
				}
			}
			uni.hideLoading(); 
			callback(result);
		},
		fail: (error, code) => {
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
		},
		complete: () => {
			
		}
	});
}

const get = (url,params, callback) => {
	uni.showLoading({
		title: ''
	});
	console.log("url=============="+url)
	var requestHeader=new Object();
	requestHeader.Accept='application/json';
	requestHeader["Content-Type"]='application/x-www-form-urlencoded';
	
	url=common.builderUrlParams(url,params) ;
	console.log("url=============="+url)
	uni.request({
		url: url,
		header: requestHeader,
		method: 'GET',
		success: (response) => {
			uni.hideLoading();
			callback(response.data);
		},
		fail: (error) => {
			console.log(""+JSON.stringify(error.response))
			uni.hideLoading();
			if (error && error.response) {
				showError(error.response);
			}
			callback();
		},
		complete: () => {
		}
	});
}

const showError = error => {
	let errorMsg = ''
	switch (error.status) {
		case 400:
			errorMsg = ''
			break
		case 401:
			errorMsg = ''
			break
		case 403:
			errorMsg = ''
			break
		case 404:
			errorMsg = `: ${error.config.url}`
			break
		case 408:
			errorMsg = ''
			break
		case 500:
			errorMsg = ''
			break
		case 501:
			errorMsg = ''
			break
		case 502:
			errorMsg = ''
			break
		case 503:
			errorMsg = ''
			break
		case 504:
			errorMsg = ''
			break
		case 505:
			errorMsg = 'HTTP'
			break
		default:
			if(error.msg){
				errorMsg = error.msg
			}else{
				errorMsg=""
			}
			break
	}

	uni.showToast({
		title: errorMsg,
		icon: 'none',
		duration: 2000
	});
}
